import { Component,  Input, Output, EventEmitter, forwardRef  } from '@angular/core';
import { ControlValueAccessor, NG_VALUE_ACCESSOR } from '@angular/forms';

const INPUT_FIELD_VALUE_ACCESSOR: any = {
  provide: NG_VALUE_ACCESSOR,
  useExisting: forwardRef(() => MiniDropdownComponent),
  multi: true
}

@Component({
  selector: 'app-mini-dropdown',
  templateUrl: './mini-dropdown.component.html',
  styleUrls: ['./mini-dropdown.component.css'],
  providers: [INPUT_FIELD_VALUE_ACCESSOR]
})
export class MiniDropdownComponent implements  ControlValueAccessor{

  @Input() data:[];
  @Input() isReadOnly = false;
  
  @Output() changeValue = new EventEmitter();
  

  private innerValue: any = '';

  get value(){
    return this.innerValue;
  }

  set value(v: string){
    if(v !== this.innerValue){
      this.innerValue = v;
      this.onChangeCb(v);
    }
  }

  onBlur() {
    this.onTouchedCb;
  }

  writeValue(value: any): void {
    if (value !== this.innerValue) {
        this.innerValue = value;
    }
  }

  registerOnChange(fn: any): void {
    this.onChangeCb = fn;
  }

  registerOnTouched(fn: any): void {
    this.onTouchedCb = fn;
  }

  setDisabledState?(isDisabled:boolean) : void {
    this.isReadOnly = isDisabled; 
  }

  constructor() { }

  onChangeCb: (_:any) => void = () => {};
  onTouchedCb: (_:any) => void = () => {};

  onChange(v){
    this.changeValue.emit(v);
  }
}
